<template>
    <div class="container">
        <div id="header">

        </div>
        <div id="main">
            <div id="leftSidebar">
                <canvas id="canvas" width="300" height="300"  v-on:click="addPointFromCanvas"></canvas>
                <div class="selection">
                    <form v-on:submit.prevent="addPoint">
                        <ul>
                            <li>Select values</li>
                            <li>
                                X:
                                <input type="radio" id="x-5" value="-5" v-model="x">
                                <label for="x-5">-5</label>
                                <input type="radio" id="x-4" value="-4" v-model="x">
                                <label for="x-4">-4</label>
                                <input type="radio" id="x-3" value="-3" v-model="x">
                                <label for="x-3">-3</label>
                                <input type="radio" id="x-2" value="-2" v-model="x">
                                <label for="x-2">-2</label>
                                <input type="radio" id="x-1" value="-1" v-model="x">
                                <label for="x-1">-1</label>
                                <input type="radio" id="x0" value="0" v-model="x">
                                <label for="x0">0</label>
                                <input type="radio" id="x1" value="1" v-model="x">
                                <label for="x1">1</label>
                                <input type="radio" id="x2" value="2" v-model="x">
                                <label for="x2">2</label>
                                <input type="radio" id="x3" value="3" v-model="x">
                                <label for="x3">3</label>
                            </li>
                            <li>
                                <label for="y">Y:</label>
                                <input v-model="y" type="text" id="y" @change="validateY" :step="0.001"  maxlength="4" placeholder="-3...3" autocomplete="off"/>
                            </li>
                            <li>
                                R:
                                <input type="radio" id="r-5" value="-5" v-model="r" @change="changeR()">
                                <label for="r-5">-5</label>
                                <input type="radio" id="r-4" value="-4" v-model="r" @change="changeR()">
                                <label for="r-4">-4</label>
                                <input type="radio" id="r-3" value="-3" v-model="r" @change="changeR()">
                                <label for="r-3">-3</label>
                                <input type="radio" id="r-2" value="-2" v-model="r" @change="changeR()">
                                <label for="r-2">-2</label>
                                <input type="radio" id="r-1" value="-1" v-model="r" @change="changeR()">
                                <label for="r-1">-1</label>
                                <input type="radio" id="r0" value="0" v-model="r" @change="changeR()">
                                <label for="r0">0</label>
                                <input type="radio" id="r1" value="1" v-model="r" @change="changeR()">
                                <label for="r1">1</label>
                                <input type="radio" id="r2" value="2" v-model="r" @change="changeR()">
                                <label for="r2">2</label>
                                <input type="radio" id="r3" value="3" v-model="r" @change="changeR()">
                                <label for="r3">3</label>
                            </li>
                            <li>
                                <input type="submit" value="check">
                            </li>
                        </ul>
                    </form>
                </div>
            </div>
            <div id="rightSidebar">
                <table class="results">
                    <thead>
                    <tr>
                        <th>X</th>
                        <th>Y</th>
                        <th>R</th>
                        <th>Res</th>
                    </tr>
                    </thead>
                    <tbody v-if="listOfPoints.length">
                    <tr v-for="cPoint in listOfPoints"  v-bind:key="cPoint">
                        <td>{{cPoint.x}}</td>
                        <td>{{cPoint.y}}</td>
                        <td>{{cPoint.r}}</td>
                        <td v-if="cPoint.result" class="green">IN</td>
                        <td v-else class="red">OUT</td>
                    </tr>
                    </tbody>
                    <tbody v-else>
                    <tr>
                        <td colspan="4">NO DATA</td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div id="footer">
            <a href="/" v-on:click.prevent="logout" class="logout">LOGOUT</a>
        </div>
    </div>
</template>

<script>
     // import Form from '../components/Form.vue';

    export default {
        // components:{Form},
        data() {
            return {
                x: '',
                r: 1,
                y: '',
                xValid: true,
                yValid: true,
                rValid: true,
                response: null,
                listOfPoints: []
            }
        },
        methods:{
            changeR: function(){
                if (this.r <= 0){
                    this.rValid = false;
                }
                else {
                    console.log("change R ");
                    this.drawCanvas();
                }
            },
            validateY: function () {
                if (this.y.match(/(0|[1-9][0-9]*)(\.[0-9]+)?/))
                    this.yValid = this.y > -3 && this.y < 3;

            },
            logout: function () {
                    localStorage.removeItem('currentUser');
                    this.$router.push('/');
            },
            getPointsRequest: function () {
                let base64Credential = localStorage.getItem('currentUser');
                console.log('TRYING');
                return this.$axios({
                    method: 'post',
                    url: this.$URL + 'getPoints',
                    headers: {
                        'Access-Control-Allow-Origin': '*',
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + base64Credential
                    }
                }).then(response => {
                    console.log('Я хочу обработать, полученные точки');
                    if (response.data.length){
                        this.listOfPoints.splice(0, this.listOfPoints.length, ...response.data);
                    }
                }).catch(error => {
                    console.log(error.response.data);
                });
            },
            addPointRequest: function (x, y, r) {
                let base64Credential = localStorage.getItem('currentUser');
                return this.$axios({
                    method: 'post',
                    url: this.$URL + 'addPoint',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + base64Credential
                    },
                    data: {x: x, y: y, r: r}
                }).then(response => {
                    let result = response.data.res;
                    this.drawDefinedPoint(x, y, result);
                    this.listOfPoints.unshift({x: x, y: y, r: r, result: result});
                    this.response = response;
                }).catch(error => {
                    console.log(error.response.data);
                    return false;
                });
            },
            addPointFromCanvas: function (e) {
                const point = this.getPointCoordinates(e);
                if (this.rValid){
                    console.log(point.x + ' ' +  point.y + ' ' + this.r);
                    this.addPointRequest(point.x, point.y, this.r);
                }
               else alert("R is incorrect");
            },
            addPoint: function () {
                if (this.x == null) {this.xValid = false}
                if (this.y == null) {this.yValid = false}
                if (this.r == null || this.r <= 0) {this.rValid = false}
                if (!this.xValid) alert("X is incorrect");
                if (!this.yValid) alert("Y is incorrect");
                if (!this.rValid) alert("R is incorrect");
                if (this.xValid && this.yValid && this.rValid) {
                    this.addPointRequest(this.x, this.y, this.r);
                }
            },
            drawDefinedPoint: function (x , y, result) {
                const plot_canvas = document.querySelector("#canvas");
                const context = plot_canvas.getContext("2d");
                context.beginPath();
                x = x / this.r * (300 / 3) + 300 / 2;
                y = 300 - (y / this.r * (300 / 3) + 300 / 2);
                context.arc(x, y, 2, 0, 2 * Math.PI);
                context.fillStyle = (result) ? 'rgba(255,240,245,0.98)' : '#d50c11';
                context.fill();
            },
            getPointCoordinates: function (e) {
                return {
                    x: ((e.offsetX - 150) / 100 * this.r).toFixed(2),
                    y: (-(e.offsetY - 150) / 100 * this.r).toFixed(2)
                }
            },
            checkArea: function (x, y, r) {
                return (((x <= r && x >= 0 && y >= -r/2 && y <= 0)
                    || ((y <= -x*2 + r) && y >= 0 && x >= 0)
                    || ((x*x + y*y) <= r*r && x <=0 && y <= 0)));
            },
            reDrawCanvas: function () {
                const plot_canvas = document.getElementById("plot");
                const context = plot_canvas.getContext('2d');
                context.clearRect(0, 0, plot_canvas.width, plot_canvas.height);
                this.drawCanvas();
            },
            drawCanvas: function () {
                console.log("lets draw canvas!!");
                const canvas = document.querySelector("#canvas");
                const ctx = canvas.getContext("2d");
                ctx.clearRect(0, 0, 300, 300);
                ctx.fillStyle = '#95a3b3';

                ctx.fillRect(150, 50, 100, 100);

                ctx.beginPath();
                ctx.arc(150, 150, 50, Math.PI, Math.PI+ 1/2*Math.PI);
                ctx.lineTo(150, 150);
                ctx.fill();
                ctx.closePath();

                ctx.beginPath();
                ctx.lineTo(50, 150);
                ctx.lineTo(150, 200);
                ctx.lineTo(150, 150);
                ctx.fill();
                ctx.closePath();

                ctx.strokeStyle = "black";
                ctx.lineWidth = 0.5;

                ctx.beginPath();
                ctx.moveTo(150, 0);
                ctx.lineTo(150, 300);
                ctx.closePath();
                ctx.stroke();

                ctx.beginPath();
                ctx.moveTo(0, 150);
                ctx.lineTo(300, 150);
                ctx.closePath();
                ctx.stroke();

                for (let point of this.listOfPoints) {
                    let result = this.checkArea(point.x, point.y, this.r);
                    this.drawDefinedPoint(point.x, point.y, result);
                }
            }
        },
        mounted(){
           this.getPointsRequest().then(() => {
                this.$nextTick(() => {
                    this.drawCanvas();
                });
            });
        }

    }
</script>



